<template>
  <div>
    <van-popup
      v-model="showLoanStatus"
      position="bottom"
      :style="{ height: '300px', width: '100%'}"
      get-container="body"
    >
      <van-picker
        title
        show-toolbar
        :columns="loanStatusList"
        @confirm="selectLoanStatus"
        @cancel="showLoanStatus = false"
      />
    </van-popup>
    <van-popup v-model="popupShow" position="top">
      <div class="search_form">
        <van-field
          style="border-style: solid;border-color:#D5D5D5;border-width:1px; margin-top:10px"
          v-model="loanNumber"
          label=":"
          placeholder=""
        />
        <van-field
          style="border-style: solid;border-color:#D5D5D5;border-width:1px; margin-top:10px"
          v-model="customerName"
          label=":"
          placeholder=""
        />
        <van-field
          style="border-style: solid;border-color:#D5D5D5;border-width:1px; margin-top:10px"
          v-model="credentialNumber"
          label=":"
          placeholder=""
        />
        <van-field
          style="border-style: solid;border-color:#D5D5D5;border-width:1px; margin-top:10px"
          v-model="customerMobileNumber"
          label=":"
          placeholder=""
        />
        <van-field
          readonly
          style="border-style: solid;border-color:#D5D5D5;border-width:1px; margin-top:10px"
          v-model="loanStatusValue"
          label=":"
          placeholder=""
          @click="showLoanStatus = true"
        />
        <van-field
          style="border-style: solid;border-color:#D5D5D5;border-width:1px; margin-top:10px"
          v-model="dealerName"
          label=":"
          placeholder=""
        />
        <div class="searchBtn">
          <van-button style="width:40%" block type="info" @click="toSearch"></van-button>
          <van-button style="width:40%" type="info" @click="toClear"></van-button>
        </div>
      </div>
    </van-popup>
    <!--  -->
    <van-popup v-model="popupyuanyin" position="top">
      <div style="width:90%;height:300px">
        <van-field
          :value="message"
          :label="titleLabel"
          type="textarea"
          placeholder=""
          autosize
          border
        />
      </div>
    </van-popup>
    <div class="contentBox">
      <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
        <van-list v-model="loading" :finished="finished" finished-text="!" @load="onLoad">
          <div class="searchContent" v-for="(item,index) in filterDetail" :key="index">
            <div class="item">
              <div>
                :
                <span>{{item.loanNumber}}</span>
              </div>
              <div>
                :
                <span>{{item.contractNumber}}</span>
              </div>
              <div style="display:flex">
                <div style="width:50%">
                  :
                  <span>{{item.customerName}}</span>
                </div>
                <div>
                  :
                  <span>{{item.credentialNumber}}</span>
                </div>
              </div>
              <div style="display:flex">
                <div style="width:50%">
                  :
                  <span>{{item.applyAmount}} </span>
                </div>
                <div>
                  :
                  <span>{{item.applyTerm}} </span>
                </div>
              </div>
              <div>
                :
                <span>{{item.fcName}}</span>
              </div>
              <div>
                :
                <span>{{item.productName}}</span>
              </div>
              <div>
                :
                <span>{{item.dealerName}}</span>
              </div>
              <div style="display:flex">
                <div style="width:50%">
                  :
                  <span></span>
                </div>
                <div>
                  :
                  <span>{{item.businessType == "1" ? "" : ""}}</span>
                </div>
              </div>
              <div style="display:flex">
                <div style="width:50%">
                  :
                  <span>{{item.firstSubmitDate}}</span>
                </div>
                <div>
                  :
                  <span>{{getStatue(item.loanStatus)}}</span>
                </div>
              </div>
            </div>
            <div>
              <div class="line"></div>
              <div class="edit">
                <van-button
                  v-if="item.loanStatus > 0 || item.loanStatus < 110"
                  style="flex:1"
                  @click="toDetail(item)"
                ></van-button>
                <van-button v-else style="flex:1">-</van-button>

                <van-button
                  v-if="(item.loanStatus > 0 || item.loanStatus < 110) && item.isReturn == '1'"
                  style="flex:1"
                  @click="tuihui(item)"
                ></van-button>
                <van-button v-else style="flex:1">-</van-button>

                <van-button
                  v-if="item.loanStatus >= -150 && item.loanStatus <= -110"
                  style="flex:1"
                  @click="jujue"
                ></van-button>
                <van-button v-else style="flex:1">-</van-button>
              </div>
            </div>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
    <van-tabbar route v-model="active">
      <van-tabbar-item replace to="/" icon="home-o"></van-tabbar-item>
      <van-tabbar-item replace to="/search" icon="search"></van-tabbar-item>
      <van-tabbar-item replace to="/mine" icon="friends-o"></van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { searchInfo, codeList, getWhy } from "../../request/api";
import { getUrlParam } from "../../utils/common.js";
export default {
  data() {
    return {
      title: "",
      active: 1,
      refreshing: false,
      finished: false,
      filterDetail: [],
      loading: false,
      pIndex: 0,
      statusCodeList: [],
      popupShow: false,
      popupyuanyin: false,
      message: "",
      titleLabel: "",
      // 
      loanNumber: "", //  
      customerName: "", //
      credentialNumber: "", //
      keyword: "", // 
      customerMobileNumber: "", // 
      loanStatus: 0, //  -
      dealerName: "", // 
      type: "", // 
      showLoanStatus: false,
      loanStatusValue: " ",
      loanStatusList: ["", "", ""]
    };
  },
  created() {
    this.title = "";
  },
  mounted() {
    //jsapp  //JS
    window.showCarLoanListSearch = res => {
      this.showSearch();
    };
    codeList({ codeType: "node" }).then(res => {
      this.statusCodeList = res.data.data;
    });
  },
  methods: {
    tuihui(item) {
      this.titleLabel = ":";
      this.popupyuanyin = true;
      this.message = "";
      getWhy({ loanNumber: item.loanNumber }).then(res => {
        this.message = res.data.data.approveDesc;
      });
    },
    jujue(item) {
      this.titleLabel = ":";
      this.popupyuanyin = true;
      this.message = "";
      getWhy({ loanNumber: item.loanNumber }).then(res => {
        this.message = res.data.data.approveDesc;
      });
    },
    getValue() {
      switch (this.loanStatus) {
        case 0:
          this.loanStatusValue = "";
          break;
        case 1:
          this.loanStatusValue = "";
          break;
        case 2:
          this.loanStatusValue = "";
          break;
      }
    },
    selectLoanStatus(val) {
      if (val == "") {
        this.loanStatus = 0;
      } else if (val == "") {
        this.loanStatus = 1;
      } else if (val == "") {
        this.loanStatus = 2;
      }
      this.showLoanStatus = false;
      this.getValue(this.loanStatus);
    },
    getStatue(status) {
      for (let i in this.statusCodeList) {
        if (status == this.statusCodeList[i].codeKey) {
          return this.statusCodeList[i].codeValue;
          break;
        }
      }
    },
    showSearch() {
      this.popupShow = !this.popupShow;
    },
    toDetail(item) {
      this.$store.state.loanNumber = item.loanNumber;
      this.$router.push({
        path: "/menu"
      });
    },
    //
    toSearch() {
      this.popupShow = false;
      this.pIndex = 1;
      this.filterDetail = [];
      this.getData();
    },
    //
    toClear() {
      this.popupShow = false;
      this.loanNumber = ""; //  
      this.customerName = ""; //
      this.credentialNumber = ""; //
      this.keyword = ""; // 
      this.customerMobileNumber = ""; // 
      this.loanStatus = 0; //  -
      this.dealerName = ""; // 
      this.type = ""; // 
      this.filterDetail = [];
      this.pIndex = 1;
      this.getData();
    },
    //
    onRefresh() {
      this.pIndex = 1;
      this.finished = false;
      this.loading = true;
      this.getData();
    },
    //
    onLoad() {
      this.pIndex += 1;
      this.getData();
    },
    getData() {
      if (this.finished) {
        this.finished = false;
        this.loading = true;
      }
      var params = {
        loanNumber: this.loanNumber,
        customerName: this.customerName,
        credentialNumber: this.credentialNumber,
        customerMobileNumber: this.customerMobileNumber,
        loanStatus: this.loanStatus,
        dealerName: this.dealerName,
        pageIndex: this.pIndex,
        pageSize: 10,
        keyword: ""
      };
      console.log(params);
      searchInfo(params).then(res => {
        let num = res.data.data.total;
        let list = res.data.data["records"];
        if (this.refreshing) {
          this.filterDetail = [];
          this.refreshing = false;
        }
        for (let i in list) {
          this.filterDetail.push(list[i]);
        }
        this.loading = false;
        console.log(this.filterDetail);
        console.log(this.filterDetail.length, num);
        if (this.filterDetail.length >= num) {
          this.finished = true;
        }
        console.log(this.finished);
      });
    },
    sure() {
      this.showResult = false;
    }
  }
};
</script>

<style scoped>
.contentBox {
  position: absolute;
  top: 0px;
  width: 100%;
  background: #f3eeee;
}
.searchContent {
  width: 90%;
  margin: 0 auto;
  font-size: 12px;
  background: #ffffff;
  height: calc(100% - 44px);
}
.item {
  margin-top: 3%;
  padding: 3%;
}
.searchBox {
  width: 60%;
}
.searchBox2 {
  width: 40%;
}
.item-icon {
  margin-top: -1px;
  margin-right: 5px;
  width: 20px;
  height: 20px;
}
.search_form {
  margin: 7% 3%;
}
.searchBtn {
  display: flex;
  margin-top: 20px;
  justify-content: space-between;
}
.nav_icon {
  position: absolute;
  right: 0;
  top: 0;
  padding: 10px 5%;
}
.calendar {
  position: absolute;
  right: 10px;
  top: 8px;
}
.navBar {
  width: 92%;
  position: fixed;
  top: 0px;
  z-index: 999;
  height: 44px;
  padding: 0 4%;
  line-height: 44px;
  background: #fff;
  color: rgb(255, 0, 51);
}
.rejectResult {
  /* margin-top: 10px; */
  padding: 16px;
  font-size: 12px;
}
.rejectResult-title {
  font-size: 18px;
  font-weight: bold;
  padding: 4px 0;
}
.rejectResult-tex {
  padding: 2px 3%;
}
.resultBtn {
  margin-top: 10px;
  width: 70%;
}
.line {
  margin-top: 5px;
  height: 1px;
  width: 100%;
  background: #ff9900;
}

span {
  color: #333333;
}
.edit {
  display: flex;
}
</style>



